@charset "UTF-8";
body {
  color: #333;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
  font-size: 14px;
  line-height: 1.6875;
  padding-top: 80px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  width: 100%;
}

@media only screen and (min-width: 768px) {
  body {
    font-size: 16px;
  }
}

html,
body,
h1,
h2,
h3,
h4,
ul,
ol,
dl,
li,
dt,
dd,
p,
div,
span,
img,
a,
table,
tr,
th,
td,
figure {
  border: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-weight: normal;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

article,
header,
footer,
aside,
figure,
figcaption,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}

@media (hover: hover) {
  a:hover {
    cursor: pointer;
    opacity: 0.7;
    text-decoration: none;
  }
}

img {
  max-width: 100%;
  vertical-align: top;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

input,
button,
textarea,
select {
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
}

input::placeholder,
textarea::placeholder {
  color: #757575;
}

input[type="submit"],
button {
  -webkit-appearance: none;
  appearance: none;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}

.l-footer {
  background: #191919;
  color: #fff;
  width: 100%;
}

.l-header {
  background: #fff;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.4);
  width: 100%;
}

.l-header__items {
  align-items: center;
  display: flex;
  height: 60px;
  justify-content: space-between;
}

.l-header--fixed {
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 20;
}

.l-inner {
  margin: 0 auto;
  max-width: 1080px;
  padding: 0 25px;
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .l-inner {
    padding: 0 40px;
  }
}

.l-sec {
  padding: 40px 0;
  width: 100%;
}

@media only screen and (min-width: 768px) {
  .l-sec {
    padding: 80px 0;
  }
}

.c-accordion-single__title {
  border-top: 1px solid #999;
  cursor: pointer;
  font-size: 20px;
  margin: 0;
  padding: 20px;
  position: relative;
}

.c-accordion-single__title::after {
  border-right: solid 2px #999;
  border-top: solid 2px #999;
  content: "";
  display: block;
  height: 8px;
  position: absolute;
  right: 25px;
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(135deg);
  transform: translateY(-50%) rotate(135deg);
  transition: all 0.2s ease-in-out;
  width: 8px;
}

.c-accordion-single__content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}

.c-accordion-single__text {
  padding: 20px;
}

.c-accordion-single__item.is-open .c-accordion-single__content {
  max-height: 150px;
}

.c-accordion-single__item.is-open .c-accordion-single__title::after {
  -webkit-transform: translateY(-50%) rotate(315deg);
  transform: translateY(-50%) rotate(315deg);
}

.c-accordion__title {
  background-color: #191919;
  border: 1px solid transparent;
  color: #fff;
  cursor: pointer;
  font-size: 20px;
  padding: 15px 20px;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.c-accordion__title::before,
.c-accordion__title::after {
  background-color: #fff;
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  right: 25px;
  top: 50%;
  width: 15px;
}

.c-accordion__title::after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  transition-duration: 0.3s;
}

.c-accordion__title:hover,
.c-accordion__title:active,
.c-accordion__title.is-active {
  background-color: #00aaa7;
}

.c-accordion__title.is-active::before {
  opacity: 0;
}

.c-accordion__title.is-active::after {
  -webkit-transform: rotate(0);
  transform: rotate(0);
}

.c-accordion__content {
  border: 1px solid #00aaa7;
  height: 0;
  line-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px;
  transition-duration: 0.3s;
}

.c-accordion__content.is-open {
  border-left: 1px solid #00aaa7;
  border-right: 1px solid #00aaa7;
  /* numberに書き換える */
  height: auto;
  line-height: normal;
  opacity: 1;
  padding: 20px;
}

.c-hamburger {
  height: 26px;
  outline: none;
  padding: 0;
  position: fixed;
  right: 25px;
  top: 17px;
  transition: transform 0.3s ease-in-out;
  width: 32.5px;
  z-index: 5;
}

.c-hamburger:focus {
  outline: none;
}

@media only screen and (min-width: 768px) {
  .c-hamburger {
    display: none;
  }
}

.c-hamburger_button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
}

.c-hamburger__line {
  background-color: #666;
  bottom: 0;
  height: 2px;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
  transition: inherit;
  width: 32.5px;
}

.c-hamburger__line::before, .c-hamburger__line::after {
  background-color: inherit;
  content: "";
  display: block;
  height: 100%;
  position: absolute;
  transition: inherit;
  width: 100%;
}

.is-drawerActive .c-hamburger__menu {
  opacity: 1;
  transition: opacity 0.3s;
  visibility: visible;
  z-index: 4;
}

.is-fixed {
  overflow: hidden;
}

.c-hamburger__line::before {
  top: -10px;
}

.c-hamburger__line::after {
  top: 10px;
}

.c-hamburger[aria-expanded="true"] .c-hamburger__line {
  background-color: transparent;
}

.c-hamburger[aria-expanded="true"] .c-hamburger__line::before, .c-hamburger[aria-expanded="true"] .c-hamburger__line::after {
  background-color: orange;
  top: 0;
}

.c-hamburger[aria-expanded="true"] .c-hamburger__line::before {
  transform: rotate(45deg);
}

.c-hamburger[aria-expanded="true"] .c-hamburger__line::after {
  transform: rotate(-45deg);
}

.c-hamburger__menu {
  background-color: rgba(255, 255, 255, 0.9);
  bottom: 0;
  height: 100vh;
  opacity: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  width: 100%;
}

.c-hamburger__header {
  background: #fff;
  height: 58px;
  width: 100%;
}

.c-hamburger__logo {
  padding-left: 25px;
}

.c-hamburger__item {
  align-items: center;
  display: flex;
  height: 100%;
}

.c-hamburger__lists {
  padding: 80px 0;
  text-align: center;
}

.c-hamburger__list {
